import { Component, OnInit } from '@angular/core';
import { AuthService } from 'src/app/auth.service';
import { GlobalPracticeTest } from 'src/app/models/global-data';





@Component({
  selector: 'app-solve',
  templateUrl: './solve.component.html',
  styleUrls: ['./solve.component.css']
})


export class SolveComponent implements OnInit{
  public isWrong=false;
  public isTrue=false;
   public isempty=false;
   public favoriteSeason:string;
   public message;
   public count=0;
   public scorePoints;
   public fillid;
   public index;
   public loading=false;
   public data;
global:GlobalPracticeTest[];

practice;
  
  constructor(private authService:AuthService){}
  ngOnInit() {
    
    this.authService.getSolve().subscribe(res=>{
    console.log(res);
    this.practice=res['practiceQuestions']
    })
    this.authService.getstatusbar_1().subscribe(res=>{
      this.message=res;
      console.log(this.message);
    })
    this.fillid=this.authService.getFilterids();
    // console.log(this.fillid);
    this.data=this.fillid.split(',')
    console.log(this.data);

  }
checkanswer(){
  this.count++;
  if(this.favoriteSeason==this.practice.answer){
    this.isTrue=true;
    if(this.count==1){
      this.scorePoints=10;
    }
    else if(this.count==2){
      this.scorePoints=5;
    }
    else if(this.count==3){
      this.scorePoints=2.5;
    }
    else{
      this.scorePoints=0;
    }

    this.authService.updatePractice(this.practice._id);
    this.authService.overallupdate(this.scorePoints);
    
  }
  else{
    this.isWrong=true;
  }
}

wrong(){
  this.isWrong=false;
}

next(){
  for(let i=0;i<this.data.length;i++){
    if(this.data[i]===this.practice._id){
      this.index=i;
    }
  }

  console.log(this.data[this.index+1]);
  
if(this.data[this.index+1]===undefined){
  
  console.log('nothing to show');
}
else{
        this.authService.getnextpracticeques(this.data[this.index+1]).subscribe(res=>{
          this.practice=res['practiceQuestions'];
          
        })
    }
}
}









